.navbar-div{
   
    background-color: rgba(255,255,255,.9);
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    position: absolute;
    width: 85%;
    z-index: 5;
    height: 6em;
}
.navbar-div-big{
    background-color: rgba(255,255,255,.9);
    display: grid;
    grid-template-columns:repeat(8,0.5fr) 0.7fr;
    align-content: center;
    grid-gap: 0.7em;
    position: absolute;
    width: 95%;
    z-index: 5;
    height: 6em;
}
.navlink-big{
    text-decoration: none;
    color: inherit;
    display: grid;
    justify-content: center;
    align-content: center;
}
.navbarlink{
    text-decoration: none;
    color: inherit;
    
}
.hcs-logo{
    display: grid;
    justify-content: center;
    align-content: center;
}
.logostyle{
    height: 4rem;
    width: 6rem;
}
.logostylebig{
    height: 4rem;
    width: 6rem;
}

.sample{
    border: 2px solid red;
    height: 100%;
}
@media only screen and (max-width:1024px){
    .navbar-div{
        position: relative;
    }
}

@media only screen and (max-width:768px){
    
    .nav-parent-big{
     display: none;   
    }
    .nav-parent-small{
        width: 100%;
        display: flex;
        justify-content: center;
    }

}
@media only screen and (min-width:769px){
    .nav-parent-small{
        display: none;
    }
    .nav-parent-big{
        width: 100%;
        display: flex;
        justify-content: center;
    }
} 
.dropdown {
  padding: 20px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 18px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  z-index: 200;
}
.dropdown:hover .dropdown_menu li {
  display: block;
  z-index: 200;
}
.dropdown_menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  z-index: 200;
}
.dropdown_menu li {
  display: none;
  padding: 10px 20px;
  font-size: 16px;
  opacity: 0;
  z-index: 200;
}
.dropdown_menu li:hover {
    z-index: 200;
}
.dropdown:hover .dropdown_menu--animated {
  display: block;
  z-index: 200;
}
.dropdown_menu--animated {
  display: none;
  z-index: 200;
}
.dropdown_menu--animated li {
  display: block;
  opacity: 1;
  z-index: 200;
}
